<template>
  <div class="container-fluid">
    <div class="row py-3 py-5 d-flex flex-wrap justify-content-center">
      <Gua
        v-for="item in items"
        :key="item.id"
        @mouseover="mouseOver(item.GuaOrder)"
        @mouseout="mouseOut()"
        @click="clickGua(item.GuaOrder)"
        :guaInfo="item"
        :varianceNumber="item.GuaOrder == guaId ? 63 : 0"
        class="Gua"
      />
    </div>
  </div>
  <!-- Modal -->
<div class="modal fade" id="detailedModal" tabindex="-1" aria-labelledby="detailedModalLabel" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="detailedModalLabel">{{ content.卦序 }} {{ content.卦名 }}</h5>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">
        {{ content }}
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
      </div>
    </div>
  </div>
</div>
</template>
<script lang="ts">
import { defineComponent, onMounted, ref } from "vue"
import Gua64Data from "@/assets/json/GUA.json"
import { Gua64 } from "@/guaModel/gua/Gua64"
import { Gua64BundleType } from "@/type/DataType"
import Gua from "@/components/Gua/Gua.vue"
import { Modal } from "bootstrap"
// import json setting: https://hackernoon.com/import-json-into-typescript-8d465beded79
export default defineComponent({
  components: {
    Gua,
  },
  setup() {
    const data: Gua64BundleType[] = Gua64Data
    const items: Gua64[] = []
    const guaId = ref<number>(0)
    const content = ref<Gua64BundleType|null>(null)
    // const items: Gua64[] = [new Gua64(3)]
    for (let item of data) {
      items.push(new Gua64(item.id))
    }
    const mouseOver = (_id: string) => {
      console.log(_id)
      guaId.value = parseInt(_id)
    }
    const mouseOut = () => guaId.value = -1

    let modal: Modal | null
    onMounted(() => {
      const ele = document.getElementById("detailedModal")!
      modal = new Modal(ele)
    })
    const clickGua = (_id: string)=>{
        content.value = data[parseInt(_id)-1]
        modal?.show()
    }

    return {
      guaId,
      items,
      content,
      mouseOver,
      mouseOut,
      clickGua
    }
  },
})
</script>
